<template>
    <div class="call" v-cloak>
        <header>
          <time-reminder></time-reminder>
           <van-notice-bar
            wrapable
            :scrollable="false"
            left-icon="volume-o"
            style="font-size: 12px; padding: 4px 8px; height: 32px; line-height: 16px; text-align: left"
            text=""
          />
        </header>
        <main>
            <template v-if="model === 'bothSides'">
              <!-- -->
              <div class="Client-Area">
                  <div id="CLIENT-AREA" ></div>
              </div>
              <!---->
              <div class="Agent-Area" >
                  <div id="AGENT-AREA" ></div>
              </div>
            </template>
            <template v-else-if="model === 'manySides'">
              <!--  -->
              <div class="manySides">
                <div v-bind:class="classObject" v-for="item in manySidesList" :key="item.index" :id="item"></div>
              </div>
            </template>
            <template v-else-if="model === 'share'">
              <!--  -->
              <div class="share-box" >
                    <div id="SHARE" ></div>
              </div>
            </template>
            <template v-else-if="model === 'loading'">
                <van-loading type="spinner" color="#1989fa" size="50" style="top: 49%"/>
            </template>
        </main>
        <footer>
          <interactive @microphoneHandel = 'microphoneHandel' @cameraHandel = 'cameraHandel' @clearUnread = 'clearUnread'></interactive>
        </footer>
        <receive-buffer @updateModel="updateModel" @setVideoPos = 'setVideoPos' @deviceConfig = 'deviceConfig'></receive-buffer>
        <video-handel @onReceiveVideoCallFinish = onReceiveVideoCallFinish @onReceiveVideoCallError = onReceiveVideoCallError></video-handel>
    </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex';
import { current } from '../toolkit';
import Interactive from '../interactive/interactive.vue';
import ReceiveBuffer from '../receiveBuffer/receiveBuffer.vue';
import TimeReminder from '../timeReminder/timeReminder.vue';
import VideoHandel from '../VideoHandel/VideoHandel.vue';
let _ = require('lodash');
let presetInfo = localStorage.getItem('H5PresetData');
export default {
  name: 'Call',
  data () {
    return {
      show: true,
      model: 'loading',
      isCVLoading: true,
      isAVLoading: true,
      userList: [], // 
      camera: null,
      microphone: null,
      loading: true,
      configInfo: JSON.parse(presetInfo).config,
      businessInfo: JSON.parse(presetInfo).businessInfo,
      manySidesList: [],

      unRead: 0
    };
  },
  components: {
    TimeReminder,
    Interactive,
    ReceiveBuffer,
    VideoHandel
  },
  computed: {
    ...mapState(['userid', 'agentid', 'roomid', 'friendList']),
    classObject () {
      return {
        fourSides: this.friendList.length === 1 && this.friendList.length <= 3, //  2,3
        sixSides: this.friendList.length > 3 && this.friendList.length <= 5, //  4,5
        nineSide: this.friendList.length > 5 && this.friendList.length <= 8 //  6,7,8
      };
    }
  },
  mounted() {
    window.onbeforeunload = () => {
      this.$AnyChatH5SDK && this.$AnyChatH5SDK.logout();
    };

    console.log(this.userid, this.agentid, this.roomid);
    // function onSuccess(stream) {
    //   console.log(',');
    // }
    // function onError(error) {
    //   console.log('', error);
    // }
    // this.getUserMedia({ video: true, audio: true }, onSuccess.bind(this), onError.bind(this));
    this.microphone = this.$AnyChatH5SDK.getMicrophones()[0];
    this.camera = this.$AnyChatH5SDK.getCameras()[0];
    // 
    this.deviceConfig();
    // 
    this.setRoomOpt();
  },
  methods: {
    ...mapMutations(['setUserList', 'setMessText', 'setUnread']),
    updateModel(step) {
      if (step === 1) {
        this.model = 'bothSides';
      } else if (step === 2) {
        this.model = 'manySides';
      } else if (step === 3) {
        this.model = 'share';
      } else {
        this.model = 'loading';
      }
    },
    // getUserMedia(constraints, success, error) {
    //   console.log(navigator.mediaDevices, navigator);
    //   if (navigator.mediaDevices.getUserMedia) {
    //     // API
    //     navigator.mediaDevices.getUserMedia(constraints).then(success).catch(error);
    //   } else if (navigator.webkitGetUserMedia) {
    //     // webkit
    //     navigator.webkitGetUserMedia(constraints, success, error);
    //   } else if (navigator.mozGetUserMedia) {
    //     // Firefox
    //     navigator.mozGetUserMedia(constraints, success, error);
    //   } else if (navigator.getUserMedia) {
    //     // API
    //     navigator.getUserMedia(constraints, success, error);
    //   }
    // },
    setRoomOpt() {
      // roomOpt
      var roomOpt = {
        onRoomUserInAndOut: this.onAnyChatUserAtRoom, // 
        onRoomUserChanged: this.onAnyChatRoomOnlineUser, // 
        onRoomUserMsgReceived: this.onAnyChatUserMsgAtRoom // 
      };
      // sdkinstance.callbackFunctionRegister
      this.$AnyChatH5SDK.callbackFunctionRegister(roomOpt);
      this.enterRoom();
    },
    enterRoom() {
      this.$AnyChatH5SDK.enterRoom({
        roomId: this.businessInfo.isInvite === '0' ? this.roomid : this.configInfo.roomId,
        password: '',
        done: (result, data) => {
          if (result.code === 0) {
            setTimeout(() => {
              this.userList = window.BRAC_GetOnlineUser();
              this.setVideoPos(this.userList);
            }, 500);
          } else {
            this.$dialog.alert({
              message: result.msg,
              theme: 'round-button',
              confirmButtonColor: '#EC662B'
            }).then(() => {
              this.$emit('onError', result.msg);
            });
          }
        }
      });
    },
    deviceConfig(data) {
      try {
        this.camera.config({
          bitRate: data?.bit || 0, // intbps
          gop: 30, // int
          width: data?.width || 640, // ()
          height: data?.height || 480, // ()
          fps: data?.fps || 15, // 
          recordBitRate: 0, //   intbps
          preset: 3, // CPU
          quality: 3 // 
        });
        this.microphone.config({
          vadctrl: 1, // ,1()0
          nsctrl: 1, // ,1()0
          echoctrl: 1, // ,1()0
          agcctrl: 1, // ,1()0
          capturemode: 0 // 0 1 2 OK3 
        });
      } catch (error) {
        this.$emit('onError', '');
      }
    },
    // 
    onAnyChatUserAtRoom(data) {
      console.log(data, '');
      if (data.action === 0) {
        this.userList = this.userList.filter((item) => { return item !== data.userId; });
      } else if (data.action === 1) {
        this.userList.push(data.userId);
      }
      this.setVideoPos(this.userList);
    },
    onAnyChatRoomOnlineUser(data) {
      console.log(data, '');
    },
    onAnyChatUserMsgAtRoom(data) {
      console.log(data, '');
      let msgObj = {
        id: data.userId,
        msg: data.msg,
        time: current()
      };
      this.setMessText(msgObj);
      this.unRead++;
      this.setUnread(this.unRead);
    },
    // 
    setVideoPos(list) {
      try {
        // 
        this.camera.close();
        this.microphone.close();
      } catch (error) {
        console.log(error);
      }
      // 
      let List = _.uniqWith(list, _.isEqual);
      console.log(List, '-');
      // vuex
      this.setUserList(List);

      if (List.length < 1) { // 
        this.updateModel(0);
      } else if (List.length === 1) {
        // 
        this.updateModel(1);
        this.$nextTick(() => {
          this.camera.open({
            id: 'CLIENT-AREA'
          });
          this.microphone.open();
          // 
          this.$AnyChatH5SDK.getRemoteVideoStream({
            remoteUserId: List[0],
            renderId: 'AGENT-AREA',
            streamIndex: 0
          });
          this.$AnyChatH5SDK.getRemoteAudioStream({
            remoteUserId: List[0]
          });
        });
      } else if (List.length > 1) {
        // 
        this.manySidesList = List.map(item => {
          return ('manySide' + item).toString();
        });
        // dividview
        this.manySidesList.unshift(('manySide' + this.userid).toString());
        this.updateModel(2);

        this.$nextTick(() => {
          // 
          this.camera.open({
            id: ('manySide' + this.userid).toString()
          });
          this.microphone.open();
          // 
          List.forEach(item => {
            this.$AnyChatH5SDK.getRemoteVideoStream({
              remoteUserId: item,
              renderId: ('manySide' + item).toString(),
              streamIndex: 0
            });
            this.$AnyChatH5SDK.getRemoteAudioStream({
              remoteUserId: List[0]
            });
          });
        });
      }
    },
    // --- opt = false 
    microphoneHandel(opt) {
      opt ? window.BRAC_UserSpeakControl(-1, 1) : window.BRAC_UserSpeakControl(-1, 0);
    },
    // --- opt = false 
    cameraHandel(opt) {
      if (this.friendList.length === 1) {
        // opt ? this.camera.open({ id: 'CLIENT-AREA' }) : this.camera.close();
        if (opt) {
          window.BRAC_UserCameraControlEx(-1, 1, 0, 0, '');
          window.BRAC_SetVideoPos(-1, document.getElementById('CLIENT-AREA'), 'me-video');
        } else {
          window.BRAC_UserCameraControlEx(-1, 0, 0, 0, '');
        }
      } else {
        opt ? this.camera.open({ id: ('manySide' + this.userid).toString() }) : this.camera.close();
      }
    },
    // 
    clearUnread() {
      this.unRead = 0;
    },

    onReceiveVideoCallFinish(result) {
      this.$emit('onVideoEnd', result.msg);
    },
    onReceiveVideoCallError(result) {
      this.$emit('onError', result.msg);
    }
  }
};
</script>

<style lang="less" scoped>
@import './call.less';
</style>
