import Vue from 'vue';
import App from './App.vue';
import store from './store';
import { messageEventsRegister } from './messageEvents';
import { Button, Icon, Popup, Toast, Dialog, Loading, Notify, Overlay, Badge, NoticeBar, Field } from 'vant';
import 'vant/lib/index.css';

Vue.use(Field);
Vue.use(Button);
Vue.use(Icon);
Vue.use(Popup);
Vue.use(Toast);
Vue.use(Dialog);
Vue.use(Loading);
Vue.use(Notify);
Vue.use(Overlay);
Vue.use(Badge);
Vue.use(NoticeBar);

// import Vant from 'vant';

// Vue.use(Vant);

class AnyChatH5 {
  #el = '#app';
  #opt;
  #events;
  #sdkInstance = null;
  #vueApp = null;

  constructor (opt, events) {
    const checkResult = this.#checkParams(opt);
    if (checkResult !== 0) {
      throw new Error(checkResult);
    }
    this.#opt = opt;
    this.#events = events;
  }

  // 
  init = () => {
    return new Promise((resolve, reject) => {
      // SDK
      const initOpt = {
        appId: this.#opt.config?.appId,
        serverIp: this.#opt.config?.serverIp,
        serverPort: this.#opt.config?.serverPort,
        nickName: this.#opt.businessInfo.isInvite === '0' ? this.#opt.userInfo.userId : '', // BRAC_LoginnickName
        strUserId: this.#opt.userInfo?.userId || this.#opt.userInfo?.phone,
        onLogin: (data) => {
          // 
          store.commit('setUserId', data.userId);
          setTimeout(() => {
            resolve();
          }, 1000);
        },
        onDisConnect: (result) => {
          reject(result);
        },
        queueOpt: {
          attribute: '',
          isAutoMode: 1, // 01 
          isGlobal: 1, // 01
          isGlobalAgent: 1, // 01
          priority: 5, // 1-15
          role: 0 // 02
        }
      };
      this.#sdkInstance = window.AnyChatH5SDK.sdkInit(initOpt);
    })
      .then(this.#createVueApp)
      .then(vueApp => {
        this.#vueApp = vueApp;
        return this;
      })
      .catch(err => {
        const errMsg = err.code && err.msg ? err.msg : '';
        console.error(err, errMsg);
      });
  }

  #createVueApp = () => {
    // Vue
    Vue.prototype.$AnyChatH5SDK = this.#sdkInstance;
    document.getElementById('loading').remove();
    this.#vueApp = new Vue({
      store,
      render: h =>
        h(App, {
          // 
          props: { importData: this.#opt },
          on: this.#events
        })
    }).$mount(this.#el);

    return this;
  }

  #checkParams = opt => {
    if (!opt.config) return '';
    if (!opt.config.serverIp) return '';
    if (!opt.config.serverPort) return '';
    if (!opt.config.appId) return 'ID';
    if (!opt.userInfo?.userId) return '';
    return 0;
  }
};

window.AnyChatH5 = AnyChatH5;

// message
messageEventsRegister();
