// 
function TimeFormat(time) {
  let m = Math.floor(time / 60);
  m = m > 9 ? m : (m > 0 ? '0' + m : '00');
  let s = time % 60;
  s = s > 9 ? s : (s > 0 ? '0' + s : '00');
  return m + ':' + s;
};
// 
function current() {
  let d = new Date();
  let str = '';
  // str += d.getFullYear() + ''; // 
  // str += d.getMonth() + 1 + ''; // 011
  // str += d.getDate() + '';
  str += d.getHours() + ':';
  str += d.getMinutes() + ':';
  str += d.getSeconds();
  return str;
}
export { TimeFormat, current };
